* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Space Grotesk", sans-serif;
  font-weight: 0;
}
.canvas {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.left {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-grow: 1;
}

form {
  margin: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

img {
  display: block;
  margin: 0;
  padding: 0;
}
.main-bg {
  z-index: -1;
  width: 25vw;
  position: absolute;
  height: 100%;
}
.card {
  z-index: -1;
  width: 23.75rem;
  height: auto;
}
.back {
  margin-top: 20px;
  display: flex;
}

.front {
  display: flex;
}
.shadow {
  position: relative;
  border-radius: 12px;
  box-shadow: 0 0 20px rgb(0 0 0 / 0.6);
}
.card-text {
  position: absolute;

  z-index: 1;
  color: white;
}
p {
  margin: 0;
}
.cvc {
  font-size: 0.875rem;

  width: 20.625rem;
  text-align: right;
  top: 5.8125rem;
}

.name {
  font-size:  0.875rem;
  left: 20px;
  top:10.625rem;
}

.exp {
  font-size: 0.875rem;
  top:10.625rem;
  left: 19.375rem;
}
.card-number {
  top: 7.5rem;
  font-size: 1.75rem;
  left: 1.25rem;
}
.logo {
  left: 1.25rem;
  top: 1.25rem;
  width: 5rem;
  
}
input {
  width: 100%;
  max-width: 380px;
  font-family: "Space Grotesk", sans-serif;
  font-size: 1rem;
  border-radius: 5px;
  height: 40px;
  outline: none;
  border: 1px solid hsl(270, 3%, 87%);
  padding: 0 10px;
}
input:focus {
  border-color: hsl(278, 94%, 30%);
}
::placeholder {
  color: hsl(270, 3%, 87%);
}
label {
  margin-bottom: 5px;
  font-size: 12px;
}
.top {
  width: 380px;
  margin-bottom: 5px;
}
.cvc-label {
  margin-left: 74px;
}
.date {
  margin-right: 10px;
  width: calc(25% - 10px);
}
.cvc-input {
  width: 50%;
}
.bot {
  width: 100%;
  max-width: 380px;
  display: flex;
}
.btn {
  user-select: none;
  cursor: pointer;
  padding-top: 13px;
  border-radius: 8px;
  text-align: center;
  width: 100%;
  max-width: 380px;
  color: white;
  height: 50px;
  background-color: hsl(278, 68%, 11%);
  margin-top: 5px;
}
.wrong {
  height: 20px;
  margin-top: 5px;
  font-size: 11px;
  color: hsl(0, 100%, 66%);
}
.wrong-block {
  display: flex;
}

.yw {
  width: 182px;
}
.complete {
  margin-left: 145px;
  width: 80px;
  height: 80px;
  margin-bottom: 30px;
}

.thank {
  margin-bottom: 10px;
  color: hsl(278, 68%, 11%);
  font-size: 30px;
  width: 380px;
  text-align: center;
}
.details {
  color: hsl(279, 6%, 55%);
  width: 380px;
  text-align: center;
  margin-bottom: 40px;
}
.mobile {
  display: none;
}
@media (max-width: 800px) {
  .desktop {
    display: none;
  }
  .mobile {
    display: block;
  }
  .canvas {
    flex-direction: column;
  }

  .main-bg {
    top: 0;
    width: 100%;
    height: 35vh;
  }
  html {
    font-size: 12px;
  }
  form , .left{
    height: 40%  ;
    align-items: center;
    justify-content: center;
  }
  .back{
    top:0;
    position: absolute;
    z-index: -1;
    transform: translateX(30px);

  }
  .front{
    transform: translateX(-30px);
    top:110px;
    position: absolute;
  }
  .left{
    height: 10%;

  }
  
}
